/* ==============MovieHeader================= */
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


.movieheader{
    display: flex;
    background-color: black;
    color: beige;
    padding-top:  20px ;
    padding-bottom: 10px;  
    justify-content: space-evenly;  
    text-align: center;
    /* font-size: 30px; */

}

.form,option,input,select{
    margin-top: 5px;
    font-size: 20px;
    padding-bottom: 0px;
}



/* ========== MovieComponent ====================== */

.moviecomponent{
    min-height: 60vh;
}
.movie-randomparent{
    
    display: flex;

    flex-direction: row;
    flex-wrap: wrap;
    column-gap: 20px;
    row-gap: 20px;
    /* width: fit-content; */
    justify-content: space-evenly;
    border: 2px solid black;
    padding-top: 20px;
    padding-bottom: 20px;
    
}

img{
    width: 35vh;
    margin-left: auto;
    margin-right: auto;
}
.movierandomchild{
    border: 1px solid green;
    min-width: 40vh;
    padding: 10px;
    max-width: 40vh;
}
.movieheading{
    color: rgb(126, 38, 38);
}



/* ======   footer ============  */

.footer{
    display: flex;
    margin-top: 30px;
    background-color: gray;
    color: white;
    font-size: 30px;
    width: 100%;
    position: absolute;
    bottom: 1;
    height: 30vh ;
    justify-content: center;
}